
  @media only screen and (max-width: 700px) {

    
.MyStory{
    display: grid;
    grid-template-columns: 1fr 2fr;
    background-color: rgba(10, 10, 10, 0.397);
    backdrop-filter: blur(5px);
    
}
.MyStoryImg{
    overflow: hidden;
    height: 40vh;
    margin-top: 40%;

}
#MyStoryImg{
    width: 200%;
}


#para{
    font-size: 0.6em;
    padding: 0% 2% 1% 2%;
    text-align: justify;
    color: cadetblue;
}
#par{
    font-size: 0.6em;
    padding: 0% 2% 1% 2%;

    color: white;
    backdrop-filter: blur(50px);
    color: cadetblue;
    font-family: tFont;

}

#Abt1 {
    color: rgb(100, 92, 92);
    font-size: 0.7em;
    padding: 0% 0 3% 3%;
    font-family: ttFont;
    letter-spacing: 9px;
    text-transform: uppercase;
}


#Abt {
    color: rgb(100, 92, 92);
    font-size: 0.8em;
    padding: 0% 0 0 0;
    font-family: ttFont;
    letter-spacing: 9px;
    text-transform: uppercase;
}
#strong{
    vertical-align: bottom;
    font-size: 2em;
}

#strong2{
    font-size: 2em;
    vertical-align: top;
}

.RightSide{
    background-color: rgba(22, 22, 22, 0.527);
    padding-top: 5%;
}


  }

  






